.blog_article{padding:20px 40px}.blog_article_title{font-size:24px;font-weight:700}.blog_article_info{display:flex;font-size:12px;align-items:center;margin:20px 0 40px}.blog_article_info_view span{font-weight:700}.blog_article_info_tag{margin:0 20px}.blog_article_info_tag span{cursor:pointer;color:#646464;margin-left:8px;font-weight:700;display:inline-block;position:relative}.blog_article_info_tag span:hover{color:#57aeff}.blog_article_foreword{display:flex;min-height:80px;position:relative;border-radius:10px;align-items:center;font-style:italic;box-sizing:border-box;line-height:1.6;padding:20px 40px 20px 80px;background-color:#eeeeee}.blog_article_foreword:before{color:#a0a0a0;left:20px;top:50%;width:1em;content:'前言';font-size:20px;font-weight:700;line-height:1.5;font-style:normal;position:absolute;padding-left:10px;border-left:5px solid #a0a0a0;transform:translateY(-50%)}.blog_article_content .code{line-height:1.4;padding:20px;border-radius:10px;font-family:Arial, Helvetica, sans-serif}
